
import React, { useState, useEffect } from 'react';
import axios from 'axios';

import { UserContext } from '../Router';

function User() {

    const { token, setToken } = React.useContext(UserContext);
    const { userInfo, setUserInfo } = React.useContext(UserContext);

    useEffect(() => {

      if (token !== '') {
        const url = 'http://kei-lottery.ru/api/v1/client';
        const jwtToken = JSON.stringify(token.token).slice(1).slice(0, -1);

        fetch(url, {
          method: 'GET',
          headers: {
            'Authorization': 'Bearer ' + jwtToken
          }
        })
        .then(response => {
          console.log('Response Code:', response.status);
          return response.text();
        })
        .then(data => {
          console.log('Response Data:', data);
          setUserInfo(JSON.parse(data));
          console.log(userInfo)
        })
        .catch(error => {
          console.error('Error:', error);
        });
      } else {
        console.log(' ');
      }
    }, []);

    return (
    <>
      <section className="container mt-20 bg-white pt-20 pb-20">
        <div className="flex fc tc content">
          <h2 className=''> </h2>
        </div>
      </section>
      <hr className='container bg-white'/>
      <section className="container bg-white pt-40 pb-40">
        <div className="row user">
          <div className="item">
            <b>Login: </b>{userInfo.login}
          </div>
          <div className="item">
            <b>: </b>{userInfo.fullName}
          </div>
          <div className="item">
            <b>Email: </b>{userInfo.email}
          </div>
          <div className="item">
            <b>: </b>{userInfo.phone}
          </div>
           <button className='btn btn-empty btn-lit mr-20'></button>
        </div>
      </section>
      <section className="container mt-20 bg-white pt-20 pb-20">
        <div className="flex fc tc content">
          <h2 className=''>  </h2>
        </div>
      </section>
      <hr className='container bg-white'/>
      <section className="container bg-white pt-40 pb-40">
        <div className="row events">
          <div className="mx-20 mb-30 item">
              <div className="mb-20">
                  <img src='/6.jpg' alt="" />
              </div>
              <span className="title mb-10">   80-</span>
              <span className="date mb-10">15.04.2023 - 23.05.2023</span>
              <a className="link" href={`/event/6`}></a>
          </div>
          <div className="mx-20 mb-30 item">
              <div className="mb-20">
                  <img src='/4.jpg' alt="" />
              </div>
              <span className="title mb-10">24 </span>
              <span className="date mb-10">20.03.2023 - 25.05.2023</span>
              <a className="link" href={`/event/4}`}></a>
          </div>
        </div>
      </section>
    </>
    );
  }
  
  export default User;