import React, { useState, useEffect } from 'react';
import { BrowserRouter, Routes, Route, Navigate } from "react-router-dom";
import Cookies from 'js-cookie';

import Main from './routes/main';
import Access from './routes/access';

import Login from './routes/login';
import Registration from './routes/registration';
import Confirm from './routes/confirm';
import Recover from './routes/recover';

import Event from './routes/event';

import NotFound from './routes/notfound';

import Header from './components/header';
import Footer from './components/footer';

export const UserContext = React.createContext('');
export const ConfirmContext = React.createContext('');
export const LoginContext = React.createContext('');

function Router() {
  const [token, setToken] = useState('');

  const [userInfo, setUserInfo] = useState({});

  const [access, setAccess] = useState(false);

  // const cookieValue = Cookies.get('reg');
  // const myData = JSON.parse(cookieValue);

  const [operationId, setOperationId] = useState('');
  const [operationIdTime, setOperationIdTime] = useState('');

  const [vhod, setVhod] = useState('');
  const [redirectToLogin, setRedirectToLogin] = useState(false);

  return (
    <BrowserRouter>
      <UserContext.Provider value={{ token, setToken, userInfo, setUserInfo, access, setAccess }}>
        <ConfirmContext.Provider value={{ operationId, setOperationId, operationIdTime, setOperationIdTime}}>
          <LoginContext.Provider value={{ vhod, setVhod, redirectToLogin, setRedirectToLogin }}>
            <div className='container-main'>
              <Header />
              <Routes>
                <Route path="/" element={<Main />} />

                { token !== '' ? <Route path="user" element={<Access />} />  : ''}

                { vhod === '' ? <Route path="/login" element={<Login />} />  : ''}
                { vhod === '' ? <Route path="/registration" element={<Registration />} />  : ''}

                { operationId !== '' ? <Route path="/confirm" element={<Confirm />} />  : ''}
            
                <Route path="/recover" element={<Recover />} />

                <Route path="/event/:number" element={<Event />} />
                
                <Route path="*" element={<NotFound />} />
              </Routes>
              <Footer />
            </div>
          </LoginContext.Provider>
        </ConfirmContext.Provider>
      </UserContext.Provider>
    </BrowserRouter>
  );
}

export default Router;