import React, { useState, useEffect } from 'react';
import Cookies from 'js-cookie';

import User from './user'
import Admin from './admin'

import { ConfirmContext } from '../Router';
import { LoginContext } from '../Router';
import { UserContext } from '../Router';

function Access() {

  const { operationId, setOperationId } = React.useContext(ConfirmContext);
  const { operationIdTime, setOperationIdTime } = React.useContext(ConfirmContext);

  const { access, setAccess } = React.useContext(UserContext);

  const { vhod, setVhod } = React.useContext(LoginContext);

  useEffect(() => {
    //  Confirm

    if(Cookies.get('reg') || operationId !== '' || operationIdTime !== '') {
      Cookies.remove('reg');
      setOperationId('');
      setOperationIdTime('');
    }

    //   
    setVhod('vhod');
  }, []);

  return (
    <>
       { access ? <Admin /> : <User />}
    </>
  );
}

export default Access;
