import React, { useState, useEffect } from 'react';
import { Link, Navigate  } from "react-router-dom";
import axios from 'axios';
import Cookies from 'js-cookie';

import { Message } from 'primereact/message';
import { Button } from 'primereact/button';
import { InputText } from 'primereact/inputtext';
import { Password } from 'primereact/password';

import { UserContext } from '../Router';
import { LoginContext } from '../Router';


function Login() {

  //   
  const { redirectToLogin, setRedirectToLogin } = React.useContext(LoginContext);

  useEffect(() => {
    setRedirectToLogin(false);
  }, []);


  const [formData, setFormData] = useState({
    login: '',
    password: ''
  });

  const { token, setToken } = React.useContext(UserContext);
  const { access, setAccess } = React.useContext(UserContext);

  const [successful, setSuccessful] = useState('');

  let isValid = true;
  
  const [loginEr, setLoginEr] = useState('');
  const [passwordEr, setPasswordEr] = useState('');

  const validateForm = () => {
    let isValid = true;
    if(formData.login.length == '') {
      setLoginEr('  ');
    }else {
      if (formData.login.length < 5 || formData.login.length > 10) {
        setLoginEr(' ');
        setSuccessful(true);
        isValid = false;
      } else {
        setLoginEr('');
      }
    }

    if(formData.password.length == '') {
      setPasswordEr('  ');
    }else {
      if (formData.password.length < 7 || formData.password.length > 14) {
        setPasswordEr(' ');
        isValid = false;
      } else {
        setPasswordEr('');
      }
    }

    return isValid;
  };

  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData(prevState => ({ ...prevState, [name]: value }));
  }

  const handleSubmit = (event) => {
    event.preventDefault();
    console.log(formData);
    if (validateForm()) {
      console.log('  ');
      axios.post('http://kei-lottery.ru/security/auth', formData)
      .then(response => {
        Cookies.set('token', response.data.token, { expires: 1 });
        setToken(response.data);
        console.log(response.data);
        if(formData.login === 'admin' && formData.password == 'adminadmin!') {
          setAccess(true);
        }
        setSuccessful('ok');
        // const jsonString = JSON.stringify(response.data);
        // Cookies.set('reg', jsonString);
        // setSuccessfully(true);
      })
      .catch(error => {
        if (error.response) {
          // The request was made and the server responded with a status code
          // that falls out of the range of 2xx
          console.log(error.response.data);

          if(error.response.data === '  ') {
            setPasswordEr('  ');
            isValid = false;
          }else {
            setPasswordEr('');
          }

          if(error.response.data === `   ${formData.login}  `) {
            setLoginEr(`   ${formData.login}  `);
            isValid = false;

          }else {
            setLoginEr('');
          }
          // console.log(error.response.status);
          // console.log(error.response.headers);
        } else if (error.request) {
          // The request was made but no response was received
          console.log(error.request);
        } else {
          // Something happened in setTing up the request that triggered an Error
          console.log('Error', error.message);
        }
        // handle registration error
      }
    );
    } else {
      console.log('  ');
    }
  }

  return (
    <section className="container section-from mt-20">
      <form className="form">
        <h2></h2>
        <div className="input-group">
          <InputText name="login" placeholder='' value={formData.login} onChange={handleChange}  />
          {loginEr === '' ? '' : <Message className='mt-10 input-massage' severity="error" text={loginEr} />}
        </div>
        <div className="input-group">
          <Password className='pass' placeholder='' name="password" value={formData.password} onChange={handleChange} feedback={false} toggleMask />
          {passwordEr === '' ? '' : <Message className='mt-10 input-massage' severity="error" text={passwordEr} />}
        </div>
        <Button label="" type="submit" onClick={handleSubmit} className='btn btn-full mt-10'/>
        <Link to="/registration" className="mt-20">
          <span className='link'></span>
        </Link>
        {successful == 'ok' ? <Navigate to = '/user' replace={true}/> : ''} 
      </form>
    </section>
  );
}

export default Login;