import React, { useState, useEffect } from 'react';
import ContentLoader from "react-content-loader"
import axios from 'axios';
import EventItem from '../components/event-item'

function Admin() {
    const [isLoading, setisLoading] = React.useState(true);
    const [events, setEvents] = React.useState({});
    
    const MyLoader = (props) => (
      <ContentLoader 
        speed={2}
        width={320}
        height={303}
        viewBox="0 0 320 303"
        backgroundColor="#f3f3f3"
        foregroundColor="#ecebeb"
        {...props}
      >
        <rect x="0" y="0" rx="6" ry="6" width="320" height="200" /> 
        <rect x="0" y="220" rx="6" ry="6" width="250" height="22" /> 
        <rect x="0" y="252" rx="6" ry="6" width="200" height="19" /> 
        <rect x="0" y="281" rx="6" ry="6" width="75" height="19" />
      </ContentLoader>
    )
    
    useEffect(() => {
      axios.get('http://kei-lottery.ru/api/v1/public/event')
        .then(response => {
          // console.log(typeof(response.data)); //       
          setEvents(response.data.length ? response.data.map(event => ( <EventItem key={event.id} event={event} /> )) : <h1> </h1>);
          setisLoading(false);
        })
        .catch(error => {
          console.error(error); //     
        });
    }, []); 
  
    const skeletons = [...new Array(6)].map((_, index) => <MyLoader key={index} />);
  
    return (
    <>
      <section className="container mt-20 bg-white pt-20 pb-20">
        <div className="flex fc tc content">
          <h2 className=''> </h2>
        </div>
      </section>
      <hr className='container bg-white'/>
      <section className="container mt-20 bg-white pt-20 pb-20">
        <div className="flex fc tc content">
          <h2 className=''>  </h2>
        </div>
      </section>
      <hr className='container bg-white'/>
      <section className="container bg-white pt-40">
        <div className="row events">
        {
            isLoading ? skeletons : events
        }
        </div>
        </section>
    </>
    );
  }
  
  export default Admin;