import React, { useState, useEffect } from 'react';
import { Link, Navigate } from "react-router-dom";
import axios from 'axios';
import Cookies from 'js-cookie';

import { Message } from 'primereact/message';
import { Button } from 'primereact/button';
import { InputText } from 'primereact/inputtext';
import { InputMask } from 'primereact/inputmask';
import { Password } from 'primereact/password';

import { ConfirmContext } from '../Router';

function Registration() {

  let isValid = true;

  const [formData, setFormData] = useState({
    login: '',
    fullName: '',
    email: '',
    phone: '',
    password: ''
  });
  
  const [loginEr, setLoginEr] = useState('');
  const [fullNameEr, setFullNameEr] = useState('');
  const [emailEr, setEmailEr] = useState('');
  const [phoneEr, setPhoneEr] = useState('');
  const [passwordEr, setPasswordEr] = useState('');

  const [successfully, setSuccessfully] = useState(false);

  const { operationId, setOperationId } = React.useContext(ConfirmContext);
  const { operationIdTime, setOperationIdTime } = React.useContext(ConfirmContext);

  const validateForm = () => {
    let isValid = true;
    if (formData.login.length < 5 || formData.login.length > 10) {
      setLoginEr('    5  10 ');
      isValid = false;
    } else {
        setLoginEr('');
    }

    if (formData.fullName.length < 3 || formData.fullName.length > 40) {
      setFullNameEr('    3  40 ');
      isValid = false;
    } else {
      setFullNameEr('');
    }

    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(formData.email)) {
      setEmailEr(' email');
      isValid = false;
    } else {
      setEmailEr('');
    }

    const phoneRegex = /^7[0-9]{10}$/;
    if (!phoneRegex.test(formData.phone)) {
      setPhoneEr('  ');
      isValid = false;
    } else {
      formData.phone = formData.phone;
      setPhoneEr('');
    }

    if (formData.password.length < 7 || formData.password.length > 14) {
      setPasswordEr('    7  14 ');
      isValid = false;
    } else {
      setPasswordEr('');
    }
    return isValid;
  };

  const handleChange = (event) => {
    const { name, value } = event.target;

    if (name == 'phone') {
      setFormData(prevState => ({ ...prevState, [name]: value.replace(/[^\d+]/g, '').slice(1) }));
    }else {
      setFormData(prevState => ({ ...prevState, [name]: value }));
    }
  }

  const handleSubmit = (event) => {
    event.preventDefault();
    
    if (validateForm()) {
      axios.post('http://kei-lottery.ru/security/register', formData)
        .then(response => {
          // {operationId: '8858cc83-fcac-40e7-8978-7852d570b712', ttlMinutes: 7}
          const expires = new Date(new Date().getTime() + 7 * 60 * 1000); //   + 7 

          // const expires = new Date(new Date().getTime() + 3000);
          
          console.log(response.data);
          const jsonString = JSON.stringify(response.data);
          Cookies.set('reg', jsonString, { expires });
          setOperationId(response.data.operationId);
          setOperationIdTime(response.data.ttlMinutes);
          setSuccessfully(true);
        })
        .catch(error => {
          if (error.response) {
            // The request was made and the server responded with a status code
            // that falls out of the range of 2xx
            console.log(error.response.data);
            if(error.response.data === '     ') {
              setLoginEr('     ');
              isValid = false;
            }
            // console.log(error.response.status);
            // console.log(error.response.headers);
          } else if (error.request) {
            // The request was made but no response was received
            console.log(error.request);
          } else {
            // Something happened in setTing up the request that triggered an Error
            console.log('Error', error.message);
          }
          // handle registration error
        }
      );
    } else {
      console.log('  ');
    }
  }

  return (
    <section className="container section-from mt-20">
      <form className="form">
        <h2></h2>
        <div className="input-group">
          <InputText className={loginEr === '' ? '' : 'p-invalid'} name="login" placeholder='' value={formData.login} onChange={handleChange} />
          {loginEr === '' ? '' : <Message className='mt-10 input-massage' severity="error" text={loginEr} />}
        </div>
        <div className="input-group">
          <InputText className={fullNameEr === '' ? '' : 'p-invalid'} name="fullName" placeholder='' value={formData.fullName} onChange={handleChange} />
          {fullNameEr === '' ? '' : <Message className='mt-10 input-massage' severity="error" text={fullNameEr} />}
        </div>
        <div className="input-group">
          <InputText className={emailEr === '' ? '' : 'p-invalid'} name="email" placeholder='Email' value={formData.email} onChange={handleChange} />
          {emailEr === '' ? '' : <Message className='mt-10 input-massage' severity="error" text={emailEr} />}
        </div>
        <div className="input-group">
          <InputMask className={phoneEr === '' ? '' : 'p-invalid'} mask="+7 (999) 999-99-99" type="phone" name="phone" placeholder='' onChange={handleChange} autoComplete="new-password" />
          {phoneEr === '' ? '' : <Message className='mt-10 input-massage' severity="error" text={phoneEr} />}
        </div>
        <div className="input-group">
          <Password className={passwordEr === '' ? 'pass' : 'pass p-invalid'} placeholder='' name="password" onChange={handleChange} feedback={false} toggleMask />
          {passwordEr === '' ? '' : <Message className='mt-10 input-massage' severity="error" text={passwordEr} />}
        </div>
        <Button label="" type="submit" onClick={handleSubmit} className='btn btn-full mt-10'/>
        <Link to="/login" className="mt-20">
          <span className='link'>  </span>
        </Link>
        {successfully ? <Navigate to = "/confirm" replace={true}/> : ''}
      </form>
    </section>
  );
}

export default Registration;
