import React, { useContext, useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Navigate } from "react-router-dom";
import Cookies from 'js-cookie';

import logo from '../logo.svg';

import { UserContext } from '../Router';
import { LoginContext } from '../Router';

function Header() {
  const { token, setToken } = useContext(UserContext);
  const { access, setAccess } = React.useContext(UserContext);
  const { userInfo, setUserInfo } = React.useContext(UserContext);

  const { vhod, setVhod } = React.useContext(LoginContext);
  const { redirectToLogin, setRedirectToLogin } = React.useContext(LoginContext);

  const handleDeleteCookie = () => {
    Cookies.remove('token');
    setToken('');
    setVhod('');
    setAccess(false);
    setRedirectToLogin(true);
  };

  return (
    <header className="container px-30 navbar">
      <Link to="/">
       <img src={logo} alt=" " />
      </Link>
      {
        token == '' ? 
          <div className="btns">
            {/* <Link className='btn btn-empty btn-lit mr-20' to="/user">user</Link>
            <Link className='btn btn-empty btn-lit mr-20' to="/confirm">Confirm</Link> */}
            <Link className='btn btn-empty btn-lit mr-20' to="/login"></Link>
            <Link className='btn btn-full btn-lit' to="/registration"></Link>
          </div>
        :
        <div className="btns">
          {/* <Link className='btn btn-empty btn-lit mr-20' to="/user">user</Link>
          <Link className='btn btn-empty btn-lit mr-20' to="/confirm">Confirm</Link>
            <Link className='btn btn-empty btn-lit mr-20' to="/login"></Link>
            <Link className='btn btn-full btn-lit' to="/registration"></Link> */}
            <Link className='' to="/user">
              <div className="avatar">
                <span>{userInfo.login}</span>
                <img height='40px' width='40px' src="avatar/0.jpg" alt="" />
              </div>
            </Link>
            <button className='btn btn-empty btn-lit mr-20' onClick={handleDeleteCookie}></button>
        </div>
      }
      {redirectToLogin && <Navigate to = '/login' replace={true}/>} 
    </header>
  );
}

export default Header;