import React, { useState, useEffect } from 'react';
import { Link, Navigate } from "react-router-dom";
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import Cookies from 'js-cookie';

import { Message } from 'primereact/message';
import { Button } from 'primereact/button';
import { InputText } from 'primereact/inputtext';

import { UserContext } from '../Router';
import { ConfirmContext } from '../Router';

function Confirm() {

  const { operationId, setOperationId } = React.useContext(ConfirmContext);
  const { operationIdTime, setOperationIdTime } = React.useContext(ConfirmContext);

  const { token, setToken } = React.useContext(UserContext);

  let isValid = true;

  const [formData, setFormData] = useState({});
  
  const [otpCodeEr, setOtpCodeEr] = useState('');

  const [successfully, setSuccessfully] = useState(false);

  const validateForm = () => {
    let isValid = true;
    if(formData.otpCode.length == '') {
      setOtpCodeEr('   ');
      isValid = false;
    }else {
      if (formData.otpCode.length < 5 || formData.otpCode.length > 10) {
        setOtpCodeEr('  ');
        isValid = false;
      } else {
        setOtpCodeEr('');
      }
    }

    return isValid;
  };

  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData(prevState => ({ ...prevState, [name]: value }));
  }

  const handleSubmit = (event) => {
    event.preventDefault();

    formData.operationId = operationId;

    console.log(formData);
    
    if (validateForm()) {
      axios.post('http://kei-lottery.ru/security/confirm', formData)
      .then(response => {
        console.log(response.data);
        Cookies.set('token', response.data.token, { expires: 1 });
        setToken(response.data);
        setSuccessfully(true);
      })
      .catch(error => {
        console.log(error);
        if (error.response) {
          console.log(error.response.data);
          console.log(error.response.status);
          console.log(error.response.headers);
        } else if (error.request) {
          console.log(error.request);
        } else {
          console.log('Error', error.message);
        }
      }
    );
    } else {
      console.log('  ');
    }
  }

  return (
    <section className="container section-from mt-20">
      <form className="form">
        <h2> </h2>
        <div className="input-group">
          <InputText name="otpCode" placeholder=' ' onChange={handleChange}  />
          {otpCodeEr === '' ? '' : <Message className='mt-10 input-massage' severity="error" text={otpCodeEr} />}
        </div>
        <div className='timer mt-10 mb-10'>
              {operationIdTime} 
        </div>
        <Button label="" type="submit" onClick={handleSubmit} className='btn btn-full mt-10'/>
        {successfully ? <Navigate to = "/user" replace={true}/> : ''}
      </form>
    </section>
  );
}

export default Confirm;
