
function CarItem({event}) {
    return (
        <div className="mx-20 mb-30 item">
            <div className="mb-20">
                <img src='/7.jpg' alt="" />
            </div>
            <span className="title mb-10">{event.title}</span>
            <span className="date mb-10">{event.description} - {event.end}</span>
            <a className="link" href={`/event/${event.id}`}></a>
        </div>
    )
}

export default CarItem
